    object-fit: cover;\r\n      }\r\n    }\r\n  }\r\n\r\n  .fixed-overlay {\r\n    position: fixed;\r\n    top: $TOP_MARGIN_MOBILE;\r\n    left: $X_MARGIN/2;\r\n\r\n    #back-button {\r\n      width: 20px;\r\n      height: 20px;\r\n      margin-top: 10px;\r\n      border-left: 2px solid white;\r\n      border-bottom: 2px solid white;\r\n      transform: rotate(45deg);\r\n\r\n      cursor: pointer;\r\n\r\n      opacity: 0;\r\n      transition-duration: 1s;\r\n\r\n      &.show {\r\n        opacity: 1;\r\n      }\r\n    }\r\n  }\r\n}","/**\r\n * About page for mobile\r\n */\r\n\r\n$HORIZONTAL_MARGIN: 25px;\r\n\r\n#about-page {\r\n  position: absolute;\r\n  width: 100vw;\r\n\r\n  top: 90px;\r\n\r\n  opacity: 0;\r\n  transition-duration: 1s;\r\n\r\n  &.mobile-about-page-open {\r\n    z-index: 3;\r\n    opacity: 1;\r\n  }\r\n\r\n  #header-section {\r\n    #header-text {\r\n      font-family: 'Sprat Type Condensed';\r\n      font-weight: 100;\r\n      font-size: 36px;\r\n      letter-spacing: -0.11em;\r\n      text-transform: uppercase;\r\n    }\r\n\r\n    .bar {\r\n      display: flex;\r\n      flex-direction: row;\r\n      align-items: center;\r\n    }\r\n\r\n    .line {\r\n      display: flex;\r\n      flex-grow: 1;\r\n\r\n      border-bottom: 1px solid white;\r\n    }\r\n\r\n    #right-bar {\r\n      margin-left: $HORIZONTAL_MARGIN;\r\n    }\r\n\r\n    #left-bar {\r\n      margin-right: $HORIZONTAL_MARGIN;\r\n    }\r\n  }\r\n\r\n  .text-box {\r\n    margin-left: $HORIZONTAL_MARGIN;\r\n    margin-right: $HORIZONTAL_MARGIN;\r\n    margin-top: 24px;\r\n    font-family: $FONT_BODY;\r\n  }\r\n}","/**\r\n * Style definitions for overlayed lines and dots.\r\n */\r\n\r\n$CIRCLE_RAD: 12px;\r\n\r\n.dot-basic {\r\n  width: $CIRCLE_RAD;\r\n  height: $CIRCLE_RAD;\r\n  background-color: white;\r\n  border-radius: 100%;\r\n}","/**\r\n * Component on mobile that opens the menu\r\n */\r\n\r\n$MOBILE_MENU_OPEN_SIZE: 30px;\r\n$LEFT_MARGIN: 30px;\r\n\r\n#mobile-open-menu-wrapper {\r\n  position: fixed;\r\n  top: 34px;\r\n  left: $LEFT_MARGIN;\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: flex-start;\r\n  width: 100vw;\r\n\r\n  &.mobile-nav-menu-open #title {\r\n    opacity: 1;\r\n  }\r\n\r\n  #title {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    width: 100vw;\r\n    position: absolute;\r\n    left: -$LEFT_MARGIN;\r\n    bottom: 1px;\r\n    opacity: 0;\r\n    transition-duration: 1s;\r\n\r\n    #text {\r\n      font-family: 'Sprat Type Condensed';\r\n      font-weight: 300;\r\n      font-size: 30px;\r\n      letter-spacing: -0.12em;\r\n    }\r\n  }\r\n\r\n  #mobile-open-menu {\r\n    width: $MOBILE_MENU_OPEN_SIZE;\r\n    height: $MOBILE_MENU_OPEN_SIZE;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n\r\n    #mobile-open-menu-sign {\r\n      font-size: 40px;\r\n      margin-bottom: 10px;\r\n    }\r\n\r\n    #mobile-open-menu-border {\r\n      position: absolute;\r\n      border: 1px solid white;\r\n      transform: rotate(45deg);\r\n      width: $MOBILE_MENU_OPEN_SIZE;\r\n      height: $MOBILE_MENU_OPEN_SIZE;\r\n    }\r\n  }\r\n}","/**\r\n * Styles for the menu that shows all the lines in the show\r\n */\r\n\r\n#mobile-menu-line-list {\r\n  position: fixed;\r\n  transition-duration: 1s;\r\n  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\r\n\r\n  // Initially, the line menu is hidden and below\r\n  transform: translateY(-100%);\r\n\r\n  &.mobile-line-menu-open {\r\n    position: absolute;\r\n    transform: translateY($LINE_ELEMENT_HEIGHT*7);\r\n  }\r\n\r\n  .line-element {\r\n    display: flex;\r\n    flex-direction: row;\r\n    align-items: center;\r\n    height: $LINE_ELEMENT_HEIGHT;\r\n    width: 100%;\r\n    border-top: 1px solid white;\r\n    background-color: black;\r\n    cursor: pointer;\r\n\r\n    .bullet {\r\n      width: 8px;\r\n      height: 8px;\r\n      background-color: white;\r\n      margin: 9px;\r\n      transform: rotate(45deg);\r\n    }\r\n\r\n    .line-element-body {\r\n      margin-top: 32px;\r\n\r\n      .title {\r\n        font-size: 36px;\r\n        transform: scale(1, 1.2);\r\n      }\r\n\r\n      .designers {\r\n        height: 16px;\r\n        margin-top: 16px;\r\n        font-size: 16px;\r\n        font-family: $FONT_SUBHEAD;\r\n      }\r\n    }\r\n  }\r\n}","/**\r\n * Styles for the menu that shows all the nav links\r\n */\r\n\r\n#mobile-menu-nav-list {\r\n  position: absolute;\r\n  width: 100%;\r\n  transition-duration: 1s;\r\n  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\r\n  left: 50px;\r\n  top: 73px;\r\n\r\n  // Initially, the line menu is hidden and below\r\n  opacity: 0;\r\n  z-index: -2;\r\n\r\n  &.mobile-nav-menu-open {\r\n    opacity: 1;\r\n    z-index: 2;\r\n  }\r\n\r\n  .nav-element {\r\n    display: flex;\r\n    flex-direction: row;\r\n    align-items: center;\r\n    width: 100%;\r\n    background-color: black;\r\n    cursor: pointer;\r\n    text-decoration: none;\r\n    margin-top: 33px;\r\n\r\n    &.active .nav-element-body {\r\n      .title {\r\n        opacity: 1;\r\n      }\r\n\r\n      .right-bar {\r\n        transform: translateX(0);\r\n      }\r\n    }\r\n\r\n    .nav-element-body {\r\n      display: flex;\r\n      flex-direction: row;\r\n      align-items: stretch;\r\n      width: 100%;\r\n\r\n      .title {\r\n        display: flex;\r\n        margin-right: 30px;\r\n\r\n        color: white;\r\n        font-size: 48px;\r\n        transform: scale(1, 1.2);\r\n\r\n        opacity: 0.65;\r\n      }\r\n\r\n      .right-bar {\r\n        display: flex;\r\n        flex-direction: row;\r\n        align-items: center;\r\n        flex-grow: 1;\r\n        transform: translateX(100vw);\r\n        transition-duration: 500ms;\r\n        transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\r\n\r\n        .line {\r\n          border-bottom: 1px solid white;\r\n          height: 1px;\r\n          width: 100%;\r\n          margin-bottom: 1px;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
